/* Additional styles for React components */

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Upload area drag and drop styles */
.upload-area.dragover {
  border-color: #764ba2;
  background-color: #f8f9ff;
  transform: translateY(-2px);
}

/* Upload hint styling */
.upload-hint {
  font-size: 0.9rem;
  color: #667eea;
  font-weight: 500;
  margin-top: 10px;
}

/* Upload progress section */
.upload-progress {
  margin: 20px 0;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.upload-progress h4 {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 1.1rem;
}

.progress-item {
  margin-bottom: 15px;
  padding: 10px;
  background: white;
  border-radius: 6px;
  border-left: 4px solid #667eea;
}

.progress-item.success {
  border-left-color: #28a745;
}

.progress-item.error {
  border-left-color: #dc3545;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.filename {
  font-weight: 500;
  color: #333;
  font-size: 0.9rem;
}

.progress-message {
  font-size: 0.8rem;
  color: #666;
  margin-top: 5px;
}

.progress-item.success .progress-message {
  color: #28a745;
}

.progress-item.error .progress-message {
  color: #dc3545;
}

/* Button hover effects */
.upload-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
}

.refresh-btn:hover:not(:disabled) {
  background-color: #5a6fd8;
}

.action-btn:hover {
  background-color: #f0f0f0;
}

/* Status message animations */
.status-message {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 12px 20px;
  border-radius: 8px;
  color: white;
  font-weight: 500;
  z-index: 1000;
  animation: slideIn 0.3s ease-out;
}

.status-success {
  background-color: #28a745;
}

.status-error {
  background-color: #dc3545;
}

.status-info {
  background-color: #17a2b8;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Document item hover effects */
.document-item:hover {
  background-color: #f8f9ff;
}

/* Folder header styling */
.folder-header {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Responsive design improvements */
@media (max-width: 768px) {
  .main-content {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .left-panel {
    gap: 15px;
  }
  
  .upload-section, .documents-section {
    padding: 20px;
  }
  
  .storage-selector {
    padding: 12px;
  }
  
  .storage-selector h3 {
    font-size: 1rem;
  }
  
  .storage-selector p {
    font-size: 0.8rem;
  }
  
  .storage-btn {
    padding: 8px 16px;
    font-size: 0.85rem;
  }
  
  .upload-area {
    padding: 30px 20px;
  }
  
  .header h1 {
    font-size: 2rem;
  }
  
  .container {
    padding: 10px;
  }
  
  .upload-progress {
    padding: 10px;
  }
  
  .progress-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
}

/* Loading state improvements */
.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/* Empty state improvements */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.empty-state p {
  margin: 0;
}

/* Document actions improvements */
.document-actions {
  display: flex;
  gap: 8px;
}

.action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.action-btn:hover {
  transform: scale(1.1);
}

.view-btn {
  background-color: #e3f2fd;
  color: #1976d2;
}

.view-btn:hover {
  background-color: #bbdefb;
}

.delete-btn {
  background-color: #ffebee;
  color: #d32f2f;
}

.delete-btn:hover {
  background-color: #ffcdd2;
}

/* Progress bar improvements */
.progress-bar {
  position: relative;
  overflow: hidden;
  height: 6px;
  background-color: #f0f0f0;
  border-radius: 3px;
}

.progress-fill {
  position: relative;
  overflow: hidden;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  transition: width 0.3s ease;
}

.progress-item.success .progress-fill {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
}

.progress-item.error .progress-fill {
  background: linear-gradient(135deg, #dc3545 0%, #e74c3c 100%);
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );
  background-size: 50px 50px;
  animation: move 2s linear infinite;
}

@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 50px 50px;
  }
}

/* Multiple file upload improvements */
.upload-area {
  position: relative;
}

.upload-area.dragover::before {
  content: 'Drop multiple files here!';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(102, 126, 234, 0.9);
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: bold;
  z-index: 10;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.05);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

/* Preview Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 90vw;
  max-height: 90vh;
  width: 800px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #e9ecef;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 600;
}

.modal-close {
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background-color 0.2s ease;
}

.modal-close:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.modal-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  gap: 16px;
  color: #666;
}

.modal-body {
  padding: 24px;
  overflow-y: auto;
  flex: 1;
}

.document-info {
  margin-bottom: 24px;
  padding-bottom: 20px;
  border-bottom: 1px solid #e9ecef;
}

.document-info h4 {
  margin: 0 0 12px 0;
  font-size: 1.3rem;
  color: #333;
  font-weight: 600;
}

.document-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 12px;
  font-size: 0.9rem;
  color: #666;
}

.document-meta span {
  background-color: #f8f9fa;
  padding: 4px 8px;
  border-radius: 4px;
  border: 1px solid #e9ecef;
}

.category {
  background-color: #e3f2fd !important;
  color: #1976d2 !important;
  border-color: #bbdefb !important;
}

.subcategory {
  background-color: #f3e5f5 !important;
  color: #7b1fa2 !important;
  border-color: #e1bee7 !important;
}

.description {
  margin: 0;
  color: #555;
  font-style: italic;
  line-height: 1.5;
}

.document-content h5 {
  margin: 0 0 16px 0;
  font-size: 1.1rem;
  color: #333;
  font-weight: 600;
}

.content-text {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 16px;
  max-height: 400px;
  overflow-y: auto;
}

.content-text pre {
