/* Storage Location Selector */
.storage-selector {
  background: linear-gradient(135deg, #f8f9ff 0%, #e8f4fd 100%);
  border: 2px solid #e9ecef;
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.storage-selector h3 {
  margin: 0 0 6px 0;
  color: #333;
  font-size: 1.1rem;
  font-weight: 600;
}

.storage-selector p {
  margin: 0 0 12px 0;
  color: #666;
  font-size: 0.85rem;
}

.storage-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.current-storage {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 10px 12px;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
}

.storage-label {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
}

.storage-value {
  font-size: 0.9rem;
  color: #333;
  font-weight: 500;
}

.storage-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
  align-self: flex-start;
}

.storage-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.storage-dropdown {
  width: 100%;
  max-width: 400px;
  padding: 12px 16px;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  background: white;
  font-size: 1rem;
  color: #333;
  cursor: pointer;
  transition: all 0.2s ease;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 16px;
  padding-right: 40px;
}

.storage-dropdown:hover {
  border-color: #667eea;
}

.storage-dropdown:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.custom-path-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.current-path {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.path-label {
  font-size: 0.9rem;
  color: #666;
  font-weight: 500;
}

.path-value {
  font-size: 0.9rem;
  color: #333;
  background: #f8f9fa;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #e9ecef;
  word-break: break-all;
}

.browse-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
  align-self: flex-start;
}

.browse-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

/* File Browser Modal */
.file-browser-modal {
  width: 90vw;
  max-width: 800px;
  height: 70vh;
  max-height: 600px;
}

.file-browser-body {
  padding: 20px;
  height: calc(100% - 80px);
  display: flex;
  flex-direction: column;
}

.file-browser-path {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 20px;
  padding: 12px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.back-btn {
  background: #6c757d;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}

.back-btn:hover:not(:disabled) {
  background: #5a6268;
}

.back-btn:disabled {
  background: #adb5bd;
  cursor: not-allowed;
}

.current-path-display {
  font-size: 0.9rem;
  color: #333;
  font-family: 'Courier New', monospace;
  word-break: break-all;
}

.file-browser-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  gap: 16px;
  color: #666;
}

.file-browser-items {
  flex: 1;
  overflow-y: auto;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  background: white;
}

.file-browser-item {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid #f1f3f4;
  cursor: pointer;
  transition: background-color 0.2s ease;
  gap: 12px;
}

.file-browser-item:hover {
  background-color: #f8f9fa;
}

.file-browser-item:last-child {
  border-bottom: none;
}

.item-icon {
  font-size: 1.2rem;
  width: 24px;
  text-align: center;
}

.item-name {
  flex: 1;
  font-size: 0.95rem;
  color: #333;
}

.select-folder-btn {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.select-folder-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(40, 167, 69, 0.3);
}

